import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { healthDataService, BloodPressureRecord } from '../services/HealthDataService';
import { keyboardService } from '../services/KeyboardService';
import './AddBloodPressure.css';

const AddBloodPressure: React.FC = () => {
  const navigate = useNavigate();
  const { id } = useParams<{ id: string }>();
  const isEdit = Boolean(id);

  const [formData, setFormData] = useState({
    systolic: '',
    diastolic: '',
    pulse: '',
    date: '',
    time: '',
    note: ''
  });

  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    if (isEdit && id) {
      loadRecord(id);
    } else {
      // 
      const now = new Date();
      setFormData(prev => ({
        ...prev,
        date: now.toISOString().split('T')[0],
        time: now.toTimeString().slice(0, 5)
      }));
    }

    // 
    keyboardService.setupAllInputFields();
    keyboardService.setKeyboardResizeMode('body');
    keyboardService.setKeyboardStyle('light');

    // 
    return () => {
      keyboardService.cleanup();
    };
  }, [isEdit, id]);

  const loadRecord = async (recordId: string) => {
    try {
      const records = await healthDataService.getBloodPressureRecords();
      const record = records.find(r => r._id === recordId);
      if (record) {
        setFormData({
          systolic: record.systolic.toString(),
          diastolic: record.diastolic.toString(),
          pulse: record.pulse.toString(),
          date: record.date,
          time: record.time,
          note: record.note || ''
        });
      } else {
        alert('');
        navigate('/blood-pressure');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
      navigate('/blood-pressure');
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.systolic || isNaN(Number(formData.systolic))) {
      newErrors.systolic = '';
    } else if (Number(formData.systolic) < 50 || Number(formData.systolic) > 300) {
      newErrors.systolic = ' 50-300 ';
    }

    if (!formData.diastolic || isNaN(Number(formData.diastolic))) {
      newErrors.diastolic = '';
    } else if (Number(formData.diastolic) < 30 || Number(formData.diastolic) > 200) {
      newErrors.diastolic = ' 30-200 ';
    }

    if (!formData.pulse || isNaN(Number(formData.pulse))) {
      newErrors.pulse = '';
    } else if (Number(formData.pulse) < 30 || Number(formData.pulse) > 200) {
      newErrors.pulse = ' 30-200 ';
    }

    if (!formData.date) {
      newErrors.date = '';
    }

    if (!formData.time) {
      newErrors.time = '';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);

    try {
      const recordData: Omit<BloodPressureRecord, '_id'> = {
        systolic: Number(formData.systolic),
        diastolic: Number(formData.diastolic),
        pulse: Number(formData.pulse),
        date: formData.date,
        time: formData.time,
        note: formData.note
      };

      if (isEdit && id) {
        await healthDataService.updateBloodPressureRecord(id, recordData);
        alert('');
      } else {
        await healthDataService.addBloodPressureRecord(recordData);
        alert('');
      }

      navigate('/blood-pressure');
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // 
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  return (
    <div className="add-blood-pressure-page" style={{ backgroundColor: '#000000', minHeight: '100vh' }}>
      {/*  - Apple  */}
      <header className="custom-header" style={{ 
        backgroundColor: '#000000',
        borderBottom: '0.5px solid rgba(255, 255, 255, 0.1)',
        backdropFilter: 'blur(10px)'
      }}>
        <div className="header-content" style={{ position: 'relative', height: '44px', display: 'flex', alignItems: 'center' }}>
          {/*  -  */}
          <div 
            onClick={(e) => {
              e.preventDefault();
              e.stopPropagation();
              console.log('');
              navigate('/blood-pressure');
            }} 
            className="custom-back-btn"
            style={{
              position: 'absolute',
              left: '20px',
              top: '50%',
              transform: 'translateY(-50%)',
              background: '#000000',
              border: '1px solid #000000',
              color: '#ffffff',
              padding: '8px 12px',
              borderRadius: '8px',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              gap: '6px',
              fontSize: '16px',
              fontWeight: '500',
              minWidth: '60px',
              minHeight: '44px',
              zIndex: 1001,
              userSelect: 'none',
              WebkitUserSelect: 'none',
              touchAction: 'manipulation'
            }}
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#ffffff" strokeWidth="2">
              <path d="M19 12H5M12 19l-7-7 7-7"/>
            </svg>
            <span style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500' }}></span>
          </div>
          
          {/*  -  */}
          <div 
            className="custom-title" 
            style={{ 
              position: 'absolute',
              left: '50%',
              top: '50%',
              transform: 'translate(-50%, -50%)',
              color: '#ffffff', 
              fontSize: '20px', 
              fontWeight: '600', 
              margin: '0',
              textAlign: 'center',
              backgroundColor: 'transparent',
              zIndex: 1000
            }}
          >
            {isEdit ? '' : ''}
          </div>
          
          {/*  -  */}
          <div
            onClick={handleSubmit}
            className="custom-save-btn"
            style={{
              position: 'absolute',
              right: '20px',
              top: '50%',
              transform: 'translateY(-50%)',
              background: '#000000',
              border: '1px solid #000000',
              color: '#ffffff',
              padding: '8px 16px',
              borderRadius: '8px',
              fontSize: '16px',
              fontWeight: '500',
              cursor: loading ? 'not-allowed' : 'pointer',
              minWidth: '60px',
              minHeight: '44px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              zIndex: 1001
            }}
          >
            {loading ? '...' : ''}
          </div>
        </div>
      </header>

      {/*  */}
      <main className="main-content" style={{ backgroundColor: '#1a1a1a', color: '#ffffff' }}>
        <form onSubmit={handleSubmit} className="blood-pressure-form" style={{ backgroundColor: 'transparent' }}>
          {/*  */}
          <div className="form-section" style={{ backgroundColor: '#1c1c1e', border: '0.5px solid rgba(255, 255, 255, 0.1)', borderRadius: '12px', padding: '20px', marginBottom: '20px' }}>
            <h2 className="section-title" style={{ color: '#ffffff', fontSize: '18px', fontWeight: '600', marginBottom: '16px' }}></h2>
            <div className="blood-pressure-inputs">
              <div className="input-group">
                <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}> (mmHg)</label>
                <input
                  type="number"
                  className={`form-input ${errors.systolic ? 'error' : ''}`}
                  value={formData.systolic}
                  onChange={(e) => handleInputChange('systolic', e.target.value)}
                  onFocus={(e) => keyboardService.setupInputField(e.target)}
                  placeholder="120"
                  min="50"
                  max="300"
                  style={{
                    width: '100%',
                    padding: '12px 16px',
                    border: '1px solid #38383a',
                    borderRadius: '8px',
                    fontSize: '16px',
                    backgroundColor: '#1c1c1e',
                    color: '#ffffff'
                  }}
                />
                {errors.systolic && <span className="error-message">{errors.systolic}</span>}
              </div>
              
              <div className="input-group">
                <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}> (mmHg)</label>
                <input
                  type="number"
                  className={`form-input ${errors.diastolic ? 'error' : ''}`}
                  value={formData.diastolic}
                  onChange={(e) => handleInputChange('diastolic', e.target.value)}
                  onFocus={(e) => keyboardService.setupInputField(e.target)}
                  placeholder="80"
                  min="30"
                  max="200"
                  style={{
                    width: '100%',
                    padding: '12px 16px',
                    border: '1px solid #38383a',
                    borderRadius: '8px',
                    fontSize: '16px',
                    backgroundColor: '#1c1c1e',
                    color: '#ffffff'
                  }}
                />
                {errors.diastolic && <span className="error-message">{errors.diastolic}</span>}
              </div>
            </div>
            
            <div className="input-group">
              <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}> (bpm)</label>
              <input
                type="number"
                className={`form-input ${errors.pulse ? 'error' : ''}`}
                value={formData.pulse}
                onChange={(e) => handleInputChange('pulse', e.target.value)}
                placeholder="80"
                min="30"
                max="200"
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  border: '1px solid #38383a',
                  borderRadius: '8px',
                  fontSize: '16px',
                  backgroundColor: '#1c1c1e',
                  color: '#ffffff'
                }}
              />
              {errors.pulse && <span className="error-message">{errors.pulse}</span>}
            </div>
          </div>

          {/*  */}
          <div className="form-section" style={{ backgroundColor: '#1c1c1e', border: '0.5px solid rgba(255, 255, 255, 0.1)', borderRadius: '12px', padding: '20px', marginBottom: '20px' }}>
            <h2 className="section-title" style={{ color: '#ffffff', fontSize: '18px', fontWeight: '600', marginBottom: '16px' }}></h2>
            <div className="time-inputs">
              <div className="input-group">
                <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}></label>
                <input
                  type="date"
                  className={`form-input ${errors.date ? 'error' : ''}`}
                  value={formData.date}
                  onChange={(e) => handleInputChange('date', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '12px 16px',
                    border: '1px solid #38383a',
                    borderRadius: '8px',
                    fontSize: '16px',
                    backgroundColor: '#1c1c1e',
                    color: '#ffffff'
                  }}
                />
                {errors.date && <span className="error-message">{errors.date}</span>}
              </div>
              
              <div className="input-group">
                <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}></label>
                <input
                  type="time"
                  className={`form-input ${errors.time ? 'error' : ''}`}
                  value={formData.time}
                  onChange={(e) => handleInputChange('time', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '12px 16px',
                    border: '1px solid #38383a',
                    borderRadius: '8px',
                    fontSize: '16px',
                    backgroundColor: '#1c1c1e',
                    color: '#ffffff'
                  }}
                />
                {errors.time && <span className="error-message">{errors.time}</span>}
              </div>
            </div>
          </div>

          {/*  */}
          <div className="form-section" style={{ backgroundColor: '#1c1c1e', border: '0.5px solid rgba(255, 255, 255, 0.1)', borderRadius: '12px', padding: '20px', marginBottom: '20px' }}>
            <h2 className="section-title" style={{ color: '#ffffff', fontSize: '18px', fontWeight: '600', marginBottom: '16px' }}></h2>
            <div className="input-group">
              <label className="input-label" style={{ color: '#ffffff', fontSize: '16px', fontWeight: '500', marginBottom: '8px', display: 'block' }}> ()</label>
              <textarea
                className="form-textarea"
                value={formData.note}
                onChange={(e) => handleInputChange('note', e.target.value)}
                placeholder="..."
                rows={3}
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  border: '1px solid #38383a',
                  borderRadius: '8px',
                  fontSize: '16px',
                  backgroundColor: '#1c1c1e',
                  color: '#ffffff',
                  resize: 'vertical',
                  minHeight: '80px'
                }}
              />
            </div>
          </div>
        </form>
      </main>
    </div>
  );
};

export default AddBloodPressure;